import { NavBar }      from '../NavBar/NavBar';
import { Content }     from './Container';
import { Constants }   from '../Common/Globals';
import { IUserInfo }   from '../Common/Interfaces/UserInterface';
import { CreateUser }  from '../Common/Mutations/User';
import { useMutation } from '@apollo/client';
import { useState, useCallback } from 'react';

export const MainPage = () => {
    const [ show, setShow ]   = useState(false);
    const [ trial, setTrial ] = useState({
        id    : 0,
        email : '',
        name  : '',
        active: false,
    });

    const [ createUser ] = useMutation(CreateUser, {
        onCompleted: data => {
            console.log(data);
            setTrial({
                id    : data.createUser.id, 
                email : data.createUser.email, 
                name  : data.createUser.name, 
                active: true
            });
        },
        onError: (error) => {
            console.log(error);
        }
    })
      
    const changeTrial = useCallback(async (email:string, dialog:boolean) => {
        const input = document.getElementById('emailInp') as HTMLInputElement;
        const btn   = document.getElementById('submit') as HTMLButtonElement;

        setShow(dialog ?? false);
        if(!Constants.EmailValidation(email)) {
            input?.classList.add('is-invalid');
            return;
        }

        input.classList.remove('is-invalid');
        const userInfo:IUserInfo = {
            name    : email.split('@')[0],
            username: email,
            email   : email
        };
        setTrial({id:0, name:userInfo.name, email:userInfo.email, active:false});
        createUser({variables:{ userInfo: userInfo }});
    }, [trial]);

    return (
        <div className='container' id='test-content'>
            <NavBar  trial={trial} change={changeTrial} showDialog={show} />
            <Content trial={trial} change={changeTrial} />
        </div>
    );
}