import { useState, useEffect }    from 'react';

export const TrialBtn = ({trial, change}:{trial:any, change:any}) => {
    const [email, setEmail] = useState('');
    const date = new Date();
    let result = null;
    date.setDate(date.getDate() + 14);

    if(trial.active) {
        result = (
            <div className="d-flex flex-row mb-2" id="inp-text">
                Thanks {trial.name}, your id is {trial.id} the trial expired on {date.toDateString()}
            </div>
        );
    } else {
        result = (
            <div className="d-flex flex-row mb-2" id="inp-text">
                <div className="d-flex flex-column has-validation me-2 w-75 float-start">
                    <input id="emailInp" type="email" className="form-control" placeholder="Enter your email" value={email} onChange={(event:any) => setEmail(event.target.value)} required/>
                    <div id="validateInp" className="invalid-feedback">Invalid email.</div>
                </div>
                <button id="submit" type="button" className="btn btn-primary flat-end" onClick={(e:any) => change(email)}>Start free trial</button>
            </div>
        )
    }
        
    return result;
}