import { NewUserForm } from "../Common/NewUser";
import { useState }    from 'react';
import Container       from 'react-bootstrap/Container';
import Button          from 'react-bootstrap/Button';
import Nav             from 'react-bootstrap/Nav';
import Navbar          from 'react-bootstrap/Navbar';
import Form            from 'react-bootstrap/Form';
import Offcanvas       from 'react-bootstrap/Offcanvas';
import logo            from "../../img/logo.png";
import './NavBar.css';

export const NavBar = (props:any) => {
  const [showLabel, setShowLabel] = useState(false);
  const showUserForm = () => props.change("", true);
  const hideUserForm = () => props.change("", false);

  return (
    <>
      <Navbar expand="lg" className="navbar navbar-dark w-100" onToggle={e => setShowLabel(e)}>
        <Container fluid>
          <Navbar.Brand><img src={logo} alt="Test" width="43" height="40" /></Navbar.Brand>
          <Navbar.Toggle aria-controls="cmp-nav" />
          <Navbar.Offcanvas id="cmp-nav" aria-labelledby="offcanvasNavbarLabel-expand-sm" placement="top">
            <Offcanvas.Header closeButton className="pb-0">
              <img src={logo} alt="Test" width="43" height="40" />
            </Offcanvas.Header>
            <Offcanvas.Body>
              <Nav className="flex-grow-1 pe-3">
                <Nav.Link href='#'>Product</Nav.Link>
                <Nav.Link href='#'>Features</Nav.Link>
                <Nav.Link href='#'>Marketplace</Nav.Link>
                <Nav.Link href='#'>Company</Nav.Link>
              </Nav>
              { !props.trial.active &&
              <Form className="d-flex">
                  <Nav.Link className="text-light me-3 pt-2" href='#'>
                    <span style={{ display: showLabel ? '' : 'none'}}>Existing customer?</span> 
                    <span className={showLabel ? 'fw-bold' : ''}> Log in</span>
                  </Nav.Link>
                  <Button variant="secondary" size="sm" onClick={showUserForm}>Start free trial</Button>
              </Form>}
            </Offcanvas.Body>
          </Navbar.Offcanvas>
        </Container>
      </Navbar>
      <NewUserForm isHidden={props.showDialog} onClose={hideUserForm} trial={props.trial} change={props.change} />
    </>
  );
};