import { TrialBtn } from '../Main/TrialBtn';
import {Button, Modal} from 'react-bootstrap';
export const NewUserForm = (props:any) => {
    return (
        <Modal show={props.isHidden} onHide={props.onClose}>
            <Modal.Header closeButton>
                <Modal.Title>Start your free 14-day trial.</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <TrialBtn trial={props.trial} change={props.change} />
            </Modal.Body>
            <Modal.Footer>
                <Button onClick={props.onClose}>Close</Button>
            </Modal.Footer>
        </Modal>
    )
}
